@media screen and (max-width: 575px) {
    .first-screen{
        .text-box{
            max-width: 300px !important;
            span{
                display: none;
            }
        }
        .photos{
            display: flex;
            flex-direction: column;
            align-items: center;
            img{
                width: 184px;
                height: 288px;
                margin-bottom: 20px;
                
            }
        }

    }
    .second-part{
        .container{
            
            .wave-box{
                top: -22px;
                img{
                    width: 33.35% !important;
                    max-width: 180px;
                }
            }
            .photos{
                display: flex;
                flex-direction: column;
                align-items: center;
            }
        }
        

    }
    .third-part{
        .do-group{
            .banner-1, .banner-2{
                display: flex;
                flex-direction: column;
                margin-bottom: 85px !important;
                width: calc(284px - 4px) !important;
                
                &::before{
                    bottom: -72px !important;
                }
                .gray-box,
                .gray-box-2 {
                    width: 280px !important;
                    border-radius: 8px;
                    border-top-right-radius: 0 !important;
                    border-top-left-radius: 0 !important;
                    height: 72px !important;
                    button{
                        margin: 15px auto !important;
                    }

                }
                .grad-box,
                .grad-box-2{
                    width: 280px !important;
                    border-radius: 8px;
                    border-bottom-right-radius: 0 !important;
                    border-bottom-left-radius: 0 !important;
                    img{
                        width: 40%;
                        height: 100%;
                    }
                    p{
                        text-align: center;
                        width: 230px;
                    }
                }
            }
            .banner-2{
                margin-bottom: 100px !important;
                &::before{
                    bottom: -72px !important;
                }
            }
        }
        .call-group{
            flex-direction: column;
            text-align: center;
            align-items: center;
            button{
                margin-top: 10px;
                margin-bottom: 5px;
            }
        }
    }
    
}
