.second-part {


    .container {
        position: relative;
        
        background-image: linear-gradient(to bottom, #28293D, #373950);
        color: #fff;
        padding-top: 20px;
        padding-bottom: 20px;

        .wave-box {
            display: flex;
            position: absolute;
            top: -32px;
            width: 100%;
            left: 0px;

            img {
                width: 100%;
            }
        }


        .text-box {
            padding-top: 20px;
            text-align: center;
            max-width: 544px;
            margin: auto;

            p {
                font-size: 16px;
                line-height: 20px;
                opacity: 80%;
            }
        }

        .photos {
            text-align: center;
            display: flex;
            justify-content: center;

            .item {
                &-img {
                    position: relative;

                    .black-box {
                        position: absolute;
                        background-color: #000;
                        color: #06C270;
                        bottom: 0px;
                        width: 100%;
                        border-bottom-left-radius: 8px;
                        border-bottom-right-radius: 8px;
                        height: 24px;
                        opacity: 86%;
                        font-size: 12px;
                        line-height: 16px;
                        font-weight: 500;
                        letter-spacing: 0.2px;
                        padding-top: 3px;
                    }
                }

                margin: 12px;

                .game-title {
                    display: flex;
                    margin-top: 5px;

                    img {
                        margin-right: 10px;
                        margin-top: 4px;
                        width: 16px;
                        height: 16px;
                    }
                }
            }
        }

        button {
            margin: auto;
            background-color: #DA2E71;
            color: #fff;
            border-radius: 8px;
            width: 190px;
            height: 50px;
            padding: 8px 12px 8px 12px;
            font-size: 14px;
            line-height: 24px;
            font-weight: 700;
            display: block;
        }
    }

}
