.third-part {

    .container {
        background-image: linear-gradient(to bottom, #494C6A, #252638);
        color: #fff;
        padding-top: 20px;
        padding-bottom: 20px;

        .line {
            width: 100%;
            margin: auto;
            height: 0;
            border-top: 1px dashed #ffffff99;
        }

        .do-group {

            .banner-1,
            .banner-2 {
                position: relative;
                width: calc(508px - 4px);
                height: 108px;
                display: flex;
                margin: 0 auto 30px;

                z-index: 0;
                border-radius: 8px;

                &::before {
                    content: "";
                    position: absolute;
                    top: -1px;
                    left: -1px;
                    bottom: -1px;
                    right: -1px;
                    background-image: linear-gradient(to bottom, #FF3B3B, #6600CC);
                    z-index: -1;
                    border-radius: 8px;

                }

                &::after{
                    content: url(assets/img/corner-icon.png);
                    position: absolute;
                    top: -10px;
                    right: -15px;
                }

                h6 {
                    font-size: 14px;
                    line-height: 16px;
                    font-weight: 700;
                }

                p {
                    font-size: 12px;
                    line-height: 16px;
                    opacity: 80%;
                    width: 235px;
                }

                .grad-box,
                .grad-box-2 {
                    padding: 16px;
                    width: 298px;
                    height: 100%;
                    background-image: linear-gradient(to bottom, #FF3B3B, #6600CC);
                    margin: 0;
                    border-top-left-radius: 8px;
                    border-bottom-left-radius: 8px;
                    p{
                        .under {
                            text-decoration: underline;
                            font-weight: 700;
                        }
                    }

                    .row-1 {
                        display: flex;
                    }

                    img {
                        margin-right: 8px;
                    }
                }

                .grad-box-2 {
                    background-image: linear-gradient(to bottom, #3E7BFA, #6600CC);
                }

                .gray-box,
                .gray-box-2 {
                    width: 210px;
                    height: 100%;
                    background-color: rgb(82 84 108);
                    border-top-right-radius: 8px;
                    border-bottom-right-radius: 8px;
                    

                    button {
                        background-image: linear-gradient(to bottom, #FF3B3B, #6600CC);
                        color: #fff;
                        border-radius: 10px;
                        width: 178px;
                        height: 40px;
                        display: block;
                        margin: 35px auto;
                    }
                }
            }

            .banner-2 {
                height: 72px;

                .grad-box-2 {
                    h6 {
                        width: 460px;                  ;

                        .under {
                            text-decoration: underline;
                        }
                    }
                }

                .gray-box-2 {
                    button {
                        background-image: linear-gradient(to bottom, #3E7BFA, #6600CC);
                        margin: 15px auto;
                    }
                }
            }


        }

        .call-group {
            max-width: 508px;
            display: flex;
            justify-content: space-between;
            margin: 15px auto 15px;

            p {
                font-size: 12px;
                line-height: 16px;
                margin-bottom: 2px;
            }

            h4 {
                font-size: 14px;
                line-height: 24px;
                font-weight: 700;
                margin-bottom: 0px;
            }

            button {
                width: 170px;
                height: 40px;
                font-size: 14px;
                border-radius: 8px;
                background-image: linear-gradient(to bottom, #FF3B3B, #6600CC);
                color: #fff;

                img {
                    margin-right: 5px;
                }
            }
        }

        .small-text {
            font-size: 10px;
            line-height: 16px;
            text-align: center;
            color: #818296;
            max-width: 506px;
            margin: 20px auto 0;

            p {
                margin-bottom: 0;
            }
        }
    }

}
